#TitleBar {
  font-family: "Times New Roman", Times, serif;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(100px, auto);
  background: -webkit-gradient(linear, left top, right top, from(#1a4c81), to(#022070));
  background: -webkit-linear-gradient(left, #1a4c81, #022070);
  background: -o-linear-gradient(left, #1a4c81, #022070);
  background: linear-gradient(to right, #1a4c81, #022070);
  height: 200px;
  border-radius: 3px;
  margin-top: 10px;
  margin-bottom: 10px;
  -ms-flex: none;
      flex: none; }

.title {
  color: white;
  font-style: italic;
  font-size: 70px;
  grid-column: 1 / 4;
  grid-row: 1;
  margin: 0 auto; }

.title .start {
  margin-top: 2px;
  height: auto; }

.title .end {
  color: yellow;
  text-align: center;
  font-size: 98px;
  margin-bottom: 10px;
  margin-top: -10px; }

.logo {
  grid-column: 4;
  grid-row: 1;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center; }

.logo .logo-image {
  max-height: 190px;
  margin-top: 10px;
  height: auto;
  width: auto; }



