.navbar {
  margin-top: 10px;
  margin-bottom: 10px;
  background: -webkit-gradient(linear, left top, right top, from(#1a4c81), to(#022070));
  background: -webkit-linear-gradient(left, #1a4c81, #022070);
  background: -o-linear-gradient(left, #1a4c81, #022070);
  background: linear-gradient(to right, #1a4c81, #022070);
  border-radius: 3px;
  height: 50px;
  width: 100%; }

.navbar ul {
  list-style-type: none;
  margin: 0 0 0 20px;
  padding: 0;
  overflow: hidden; }

.navbar li {
  float: left;
  margin: 8px 3px; }

.navLink {
  -webkit-transition: .4s ease;
  -o-transition: .4s ease;
  transition: .4s ease;
  display: block;
  padding: 8px;
  text-decoration: none;
  color: white; }

.navLink:hover {
  color: yellow;
  border-bottom: 3px solid yellow; }

.navLinkCur {
  display: block;
  padding: 8px;
  text-decoration: none;
  color: yellow; }

.navLinkCur:hover {
  color: yellow; }



